.Home .lander {
    padding: 80px 0;
    text-align: center;
}

h6 {
    font-family: "Open Sans", sans-serif;
    font-weight: 600;
}

small {
    color: #999;
}

.Home .notes h4 {
    font-family: "Open Sans", sans-serif;
    font-weight: 600;
    overflow: hidden;
    line-height: 1.5;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.Home .notes p {
    color: #666;
}